body {
  background-color: #202020;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

.logo img {
  max-width: 150px;
}

.logo {
  padding: 0 50px;
  background-color: #ff0000;
}

.banner img {
  widows: 100%;
}

.team {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.team .team-details {
  padding: 10px;
  flex: 0 0 33.333%;
}

.team .team-details .content {
  border-radius: 5px;
  padding: 25px 10px;
  background-color: #000000;
  transform: scale(1);
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

.team .team-details .content:hover {
  transform: scale(1.02);
}

.team .team-details .content .team-name {
  font-size: 25px;
  font-weight: 500;
  color: #fff;
  text-align: center;
  margin-top: 15px;
}

.team .team-details .content img {
  display: block;
  width: 200px;
  margin: 0 auto;
  border-radius: 5px;
}

.container {
  padding: 0 50px;
  margin: 10px 0;
}
